.header {
    width: 100%;
    height: 60px;
    background-color: #4da5b7;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    top: 0;
    transition: top 0.3s;
}

.hidden {
    height: 10px;
}
@media (max-width: 748px) {
    .header {
        justify-content: center;
    }
}
